'use client'

import { createContext, useContext, useState, useEffect, ReactNode } from 'react'

interface AccessibilitySettings {
  fontSize: 'small' | 'medium' | 'large' | 'xlarge'
  contrast: 'normal' | 'high' | 'blue'
  voiceEnabled: boolean
  setSettings: (newSettings: Partial<Omit<AccessibilitySettings, 'setSettings'>>) => void
}

const AccessibilityContext = createContext<AccessibilitySettings | undefined>(undefined)

export const AccessibilityProvider = ({ children }: { children: ReactNode }) => {
  const [fontSize, setFontSize] = useState<AccessibilitySettings['fontSize']>('medium')
  const [contrast, setContrast] = useState<AccessibilitySettings['contrast']>('normal')
  const [voiceEnabled, setVoiceEnabled] = useState<boolean>(false)

  // Load settings from localStorage
  useEffect(() => {
    const saved = localStorage.getItem('elderease_preferences')
    if (saved) {
      const parsed = JSON.parse(saved)
      if (parsed.fontSize) setFontSize(parsed.fontSize)
      if (parsed.contrast) setContrast(parsed.contrast)
      if (parsed.voiceEnabled !== undefined) setVoiceEnabled(parsed.voiceEnabled)
    }
  }, [])

  // Apply font size & contrast to body
  useEffect(() => {
    document.body.classList.remove('text-sm', 'text-base', 'text-lg', 'text-xl')
    document.body.classList.add(
      fontSize === 'small'
        ? 'text-sm'
        : fontSize === 'medium'
        ? 'text-base'
        : fontSize === 'large'
        ? 'text-lg'
        : 'text-xl'
    )

    document.body.classList.remove('contrast-normal', 'contrast-high', 'contrast-blue')
    document.body.classList.add(
      contrast === 'normal'
        ? 'contrast-normal'
        : contrast === 'high'
        ? 'contrast-high'
        : 'contrast-blue'
    )

    // Save to localStorage
    localStorage.setItem(
      'elderease_preferences',
      JSON.stringify({ fontSize, contrast, voiceEnabled })
    )
  }, [fontSize, contrast, voiceEnabled])

  const setSettings = (newSettings: Partial<Omit<AccessibilitySettings, 'setSettings'>>) => {
    if (newSettings.fontSize) setFontSize(newSettings.fontSize)
    if (newSettings.contrast) setContrast(newSettings.contrast)
    if (newSettings.voiceEnabled !== undefined) setVoiceEnabled(newSettings.voiceEnabled)
  }

  return (
    <AccessibilityContext.Provider
      value={{ fontSize, contrast, voiceEnabled, setSettings }}
    >
      {children}
    </AccessibilityContext.Provider>
  )
}

export const useAccessibility = () => {
  const context = useContext(AccessibilityContext)
  if (!context) {
    throw new Error('useAccessibility must be used within AccessibilityProvider')
  }
  return context
}
